.field-form {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-bottom: 16px;
  
    label {
      font-size: 16px;
      color: #333;
      font-weight: bold;
    }
  
    .input-field {
      padding: 12px 10px;
      font-size: 14px;
      border: 2px solid #ccc;
      border-radius: 4px;
      transition: border-color 0.3s;
  
      &:focus {
        border-color: #4f46e5;
        outline: none;
      }
    }
  }