.template-answer {
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    overflow: hidden;
  
    .answer-area {
      width: 100%;
      height: 400px;
      padding: 12px;
      font-size: 14px;
      color: #333;
      border: 2px solid #ccc;
      border-radius: 4px;
      background-color: white;
      resize: none;
      box-sizing: border-box;
  
      &:focus {
        border-color: #4f46e5;
        outline: none;
      }
    }
  }