.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #FAFAFA;
    font-family: 'Arial', sans-serif;

    .login-form {
        margin-top: 20px;
        padding: 40px;
        background-color: white;
        border-radius: 10px;
        box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
        width: 100%;
        max-width: 350px;
        display: flex;
        flex-direction: column;
        gap: 20px;

        h2 {
            color: #4F46E5;
            text-align: center;
        }

        .form-group {
            display: flex;
            flex-direction: column;

            label {
                font-weight: bold;
                margin-bottom: 5px;
            }

            input {
                padding: 10px;
                border: 2px solid #ccc;
                border-radius: 5px;
                font-size: 16px;

                &:focus {
                    border-color: #4F46E5;
                    outline: none;
                }
            }
        }

        .login-button {
            padding: 10px;
            background-color: #4F46E5;
            color: white;
            border: none;
            border-radius: 5px;
            cursor: pointer;
            transition: background-color 0.3s;

            &:hover {
                background-color: #3730A3;
            }
        }
        .signup-prompt {
            text-align: center;
            margin-top: 20px;

            .signup-link {
                color: #4F46E5;
                font-weight: bold;
                text-decoration: none;

                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}

// 반응형 디자인
@media (max-width: 600px) {
    .login-form {
        padding: 20px;
        width: 90%;
    }
}