.template-form {
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

    .form-title {
        font-size: 32px;
        font-weight: bold;
    
        .form-name {
            color: #4f46e5;
        }
      }
  
    .generate-answer-btn {
      padding: 10px 20px;
      font-size: 16px;
      background-color: #4f46e5;
      color: white;
      border: none;
      border-radius: 4px;
      cursor: pointer;
      transition: background-color 0.3s;
  
      &:hover {
        background-color: #3730a3;
      }
    }
  }