.template-block {
    padding: 10px 15px;
    background-color: #ffffff;
    font-size: 16px;
    font-family: 'Arial', sans-serif;
    font-weight: 700;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s, box-shadow 0.3s;
  
    &:hover {
      background-color: #dee2e6;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
      cursor: pointer;
    }
  }