// 색상 정의
$primary-color: #5c67f2; // 밝은 파란색
$secondary-color: #f0f1f8; // 연한 그레이
$accent-color: #ff4081; // 액센트 색상

.template-layout {
  display: flex;
  height: 100vh;
  color: #333;
  background-color: $secondary-color;

  .sidebar {
    width: 250px;
    background-color: $primary-color;
    padding: 20px;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  }

  .main-content {
    flex-grow: 1;
    padding: 20px;
    background-color: white;
    overflow-y: auto;
  }
}

// 반응형 디자인
@media (max-width: 768px) {
  .template-layout {
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
    height: auto;
    box-shadow: none;
  }

  .main-content {
    width: 100%;
    padding: 15px;
  }
}